export default {
    name: "newProducts",
    data () {
        return {
            animate: false,
            marqueelist: [
               {
                   id: '1',
                   name: '1.18号0点上新&nbsp;少女beauty&nbsp;life'
               }, {
                   id: '2',
                   name: '1.24号0点上新&nbsp;少女beauty&nbsp;life'
               }
           ],
            list:[
                {
                    id:'1',
                    src: require("../../assets/babyimages/1 (3).png"),
                    des: '樱桃小丸子联名原创蜗牛工作室大衣中长款英安必',
                    price: '12.8',
                    number: '222',
                },  {
                    id: '2',
                    src: require('../../assets/babyimages/1 (1).png'),
                    des: '第一个描述桃小丸子联名原创蜗牛工作室大衣中',
                    price: '12.8',
                    number: '222',
                },  {
                    id: '3',
                    src: require('../../assets/babyimages/1 (3).png'),
                    des: '第一个描述桃小丸子联名原创蜗牛工作室大衣中',
                    price: '12.8',
                    number: '222',
                },  {
                    id: '4',
                    src: require('../../assets/babyimages/1 (2).png'),
                    des: '桃小丸子联名原创蜗牛工作室大衣中注释',
                    price: '12.8',
                    number: '222',
                },  {
                    id: '5',
                    src: require("../../assets/babyimages/1 (2).png"),
                    des: '第一个描述',
                    price: '12.8',
                    number: '222',
                },  {
                    id: '6',
                    src: require('../../assets/babyimages/1 (1).png'),
                    des: '第一个描述',
                    price: '12.8',
                    number: '222',
                },  {
                    id: '7',
                    src: require('../../assets/babyimages/1 (1).png'),
                    des: '第一个描桃小丸子联名原创蜗牛工作室大衣中述',
                    price: '12.8',
                    number: '222',
                },  {
                    id: '8',
                    src: require('../../assets/babyimages/1 (2).png'),
                    des: '桃小丸子联名原创蜗牛工作设备',
                    price: '12.8',
                    number: '222',
                },  {
                    id: '9',
                    src: require('../../assets/babyimages/1 (3).png'),
                    des: '桃小丸子联名原创蜗牛工作室大衣中',
                    price: '12.8',
                    number: '222',
                }
            ]
        }
    },
    methods: {
      showMarquee () {
          this.animate = true
          setTimeout(() =>{
              this.marqueelist.push(this.marqueelist[0])
              this.marqueelist.shift()
              this.animate = false
          },3000)
      }
    },
    created() {
      setInterval(this.showMarquee,2000);
    }
}



